import React, { Component } from 'react'
import Child from './Child'

export default class Parent extends Component {
  
    state = {
        // userName: 'Wafaa',
        // userID: 289,
        // userAge: 28,
        productList:[
            {name: "Apple" ,price: 4000 ,category: "iPad" ,count: 1},
            {name: "Apple" ,price: 5000 ,category: "iPhone" ,count: 2},
            {name: "Apple" ,price: 5500 ,category: "Macbook Air" ,count: 3},
            {name: "Apple" ,price: 10000 ,category: "Macbook Pro" ,count: 4},
            {name: "Apple" ,price: 1000 ,category: "i Watch" ,count: 5},
            {name: "Apple" ,price: 9000 ,category: "iMac" ,count: 6}
        ]
    }
    deleteProduct=(indexPro)=>{
        // console.log("delete");
        let proList =[...this.state.productList]
        proList.splice(indexPro,1)
        this.setState({productList:proList})
    }
    updateProduct=(indexPro)=>{
        // console.log("update");
        let proList =[...this.state.productList]
        proList[indexPro].count+=100;
        this.setState({productList:proList})
    }
    render() {
    return <>
        {/* <div className="bg-warning py-5 text-center">
            <Child data={this.state.userName} data1={this.state.userAge}/>
        </div> */}
        <div className="py-5" style={{ backgroundImage: "linear-gradient(#75385A,lightgreen)"}}>
            <div className="container">
                <div className="row">
                    {this.state.productList.map((w,index)=> <Child key={index} delete={this.deleteProduct} update={this.updateProduct} indexPro={index} productData={w}/>)}
                {/* <Child productData={this.state.productList[1]}/> */}
                </div>
            </div>
        </div>
    
    </>
  }
}

